#contact {
  width: 100%;
  margin-bottom: 100px;
}
.contact_heading {
  width: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  color: var(--color-c);
  gap: 15px;
  margin-bottom: 1.5rem;
}
.contact_inner {
  width: 90%;
  max-width: 1247px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  background-color: var(--color-a);
  border-top: 1px solid var(--color-b);
  border-bottom: 1px solid var(--color-b);
  box-shadow: rgba(0.2, 0.2, 0.2, 0.4) 2px 2px 4px,
    rgba(0.2, 0.2, 0.2, 0.3) 0px 7px 13px -3px,
    rgba(0.1, 0.1, 0.1, 0.2) 0px -3px 0px inset;
}
.fill_details {
  width: 60%;
}
.my_info {
  width: 40%;
  display: flex;
  align-items: center;
  color: var(--color-c);
  border-left: 1px solid var(--color-b);
}
.contact_form {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: column;
}
.contact_form > label {
  color: var(--color-d);
}
.input_field {
  height: 40px;
  background-color: transparent;
  outline: none;
  color: var(--color-d);
  padding: 0 1rem;
  font-size: 1.1rem;
  border: none;
  border-bottom: 1px solid var(--color-b);
  margin-bottom: 1.5rem;
}
.contact_form > textarea {
  height: 40px;
  background-color: var(--color-a);
  outline: none;
  color: var(--color-d);
  padding: 2rem 1rem;
  font-size: 1.1rem;
  border: none;
  margin-bottom: 1.5rem;
  border: 2px solid var(--color-b);
  border-radius: 5px;
  margin-top: 1rem;
  resize: none;
}

.contact_submit_btn {
  background-color: var(--color-c);
  height: 40px;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  color: var(--color-b);
  border: 2px solid var(--color-c);
  transition: 0.4s ease-out;
  width: 100%;
  align-self: flex-end;
  margin-bottom: 2rem;
  font-family: "Nexa", sans-serif;
  font-size: 1rem;
}
.contact_submit_btn:hover {
  background-color: var(--color-c);
  color: var(--color-b);
  border: 2px solid var(--color-c);
  transition: 0.3s ease-out;
}
.my_personal_details {
  width: 80%;
  margin: auto;
}
.my_personal_detailss {
  color: var(--color-d);
}
.my_location {
  display: flex;
  align-items: center;
  gap: 20px;
}
.phone_number {
  display: flex;
  align-items: center;
  gap: 20px;
}
.email_address {
  display: flex;
  align-items: center;
  gap: 20px;
}
.contact_social_icons {
  width: 55%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 1.5rem;
  margin-top: 4rem;
}
/* MEDIA QUERY */
@media screen and (min-width: 1025px) {
}
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .fill_details {
    width: 50%;
  }
  .my_info {
    width: 50%;
  }
  .contact_inner {
    border-top: 1px solid var(--color-a);
    border-bottom: 1px solid var(--color-a);
  }
}
@media screen and (max-width: 481px) {
  .contact_inner {
    flex-direction: column;
  }
  .fill_details {
    width: 100%;
  }
  .my_info {
    width: 100%;
    padding-bottom: 2rem;
    border-left: 1px solid var(--color-a);
  }
  .contact_inner {
    border-top: 1px solid var(--color-a);
    border-bottom: 1px solid var(--color-a);
  }
}
